/*****MOBILE-FIRST*****/

.main__Countri-Detail {
  margin: 81px 0 0 0;
  padding: 0 25px 28px 25px;
}

.main__Link-Back {
  display: flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  color: var(--color-text);
  background-color: var(--color-elements);
  font-family: inherit;
  font-weight: 300;
  font-size: 1.4rem;
  margin: 0 0 128px 0;
  box-shadow: 0px 3px 10px 2px rgba(0, 0, 0, 0.1),
    3px 0px 10px 2px rgba(0, 0, 0, 0.1);
  padding: 16px 0 15px 0;
  border-radius: 4px;
  max-width: 150px;
}

.main__Icon-Link {
  color: var(--color-text);
  margin: 0 18px 0 0;
}

.main__Ilustration-Countri {
  display: block;
  max-width: 638px;
  max-height: 458px;
  width: 100%;
  margin: 0 auto;
  box-shadow: 0.3px 0.3px 8px 2px rgba(0, 0, 0, 0.1);
}

.main__Side-Content {
  margin: 0 0 92.2px 0;
}

.main__Name-Countri {
  font-size: 2.75rem;
  font-weight: 800;
  margin: 0 0 54px 0;
  color: var(--color-text);
}

.main__Data:not(:last-child) {
  margin: 0 0 33px 0;
}

.main__Relevant-Info,
.main__Value-Info,
.main__Message-Countries-Borders {
  display: inline-block;
  font-family: inherit;
  font-size: 1.68rem;
  color: var(--color-text);
}

.main__Relevant-Info {
  font-weight: 600;
  margin: 0 6px 0 0;
}

.main__Message-Countries-Borders {
  margin: 0 0 0 8px;
}

.main__Value-Info {
  font-weight: 300;
}

.main__Container-Side:not(:last-child) {
  margin: 0 0 97px 0;
}

.main__Container {
  margin: 0 0 88px 0;
}

.main__List-Countries-Borders {
  list-style: none;
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  /*justify-content: center;*/
  gap: 20px;
}

.main__Item-Border-Countri {
  border-radius: 4px;
  box-shadow: 1px 1px 8px 2px rgba(0, 0, 0, 0.1);
  max-width: 192px;
  width: 100%;
}

.main__Item-Link-Border {
  border-radius: 4px;
  display: block;
  text-decoration: none;
  color: var(--color-text);
  background-color: var(--color-elements);
  font-size: 1.5rem;
  font-weight: 300;
  padding: 14px 30.5px;
  width: 100%;
  text-align: center;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}

.main__Relevante-Info_Text--Big {
  font-size: 1.95rem;
  margin: 0 0 39px 0;
}

/******BREKPOINT 550px TO LAYOUT TABLETS*****/
@media screen and (min-width: 550px) {
  .main__Countri-Detail {
    padding: 0 56px 28px 56px;
  }

  .main__Link-Back {
    font-size: 1.8rem;
    padding: 16px 0 15px 0;
    max-width: 208px;
  }

  .main__Icon-Link {
    color: var(--color-text);
    margin: 0 28px 0 0;
  }
}

/******BREAKPOINT 960px TO LAYOUT DESKTOP*****/
@media screen and (min-width: 960px) {
  .main__Countri-Detail {
    margin: 80px 0 0 0;
    padding: 0 80px 298px 80px;
  }

  .main__Link-Back {
    font-size: 1rem;
    margin: 0 0 80px 0;
    padding: 11px 0 11px 0;
    border-radius: 4px;
    max-width: 136px;
  }

  .main__Icon-Link {
    margin: 0 8px 0 0;
  }

  .main__Content-Detail {
    display: grid;
    grid-template-columns: minmax(min-content, 561px) minmax(min-content, 600px);
    align-items: center;
    gap: 20px;
  }

  .main__Side-Content {
    margin: 0;
  }

  .main__Name-Countri {
    font-size: 1.91rem;
    font-weight: 800;
    margin: 0 0 35px 0;
    color: var(--color-text);
  }

  .main__Relevant-Info,
  .main__Value-Info,
  .main__Message-Countries-Borders,
  .main__Item-Link-Border,
  .main__Relevante-Info_Text--Big {
    font-size: 16px;
  }

  .main__Ilustration-Countri {
    max-width: 100%; /*622px*/
    max-height: 401.817px; /*401.817px*/
    width: 100%;
    margin: 0;
  }

  .main__Data:not(:last-child) {
    margin: 0 0 13.5px 0;
  }

  .main__Container {
    display: grid;
    grid-template-columns: repeat(2, minmax(min-content, 300px));
    /*display: flex;
    flex-flow: row wrap;
    */
    align-items: flex-start;
    margin: 0 0 55px 0;
    gap: 10px;
  }

  .main__Container-Side:not(:last-child) {
    margin: 0;
  }

  .main__List-Countries-Borders {
    gap: 10px;
  }

  .main__Container:last-child {
    display: flex;
    flex-flow: row wrap;
    align-items: center;
    margin: 0;
    gap: 16px;
  }

  .main__Item-Border-Countri {
    max-width: none;
    box-shadow: 0.5px 0.5px 2px 1px rgba(0, 0, 0, 0.1);
    width: auto;
  }

  .main__Item-Link-Border {
    border-radius: 4px;
    font-size: 0.88rem;
    padding: 6px 10px 5.5px 10px;
  }

  .main__Relevante-Info_Text--Big {
    margin: 0;
  }
}

/*****BREAKPOINT TO DESKTOP LAYOUT 1400px*****/
@media screen and (min-width: 1400px) {
  .main__Detail {
    padding: 43px 0 0 0;
  }

  .main__Content-Detail {
    align-items: flex-start;
    gap: 120px;
  }

  .main__Container {
    gap: 101px;
  }
}
